@tablet-width: 768px;
@desktop-width: 1200px;
@mobile-width-only: 767px;


@primary-fonts: "PT Sans", "Arial", sans-serif;
@secondary-fonts:  "Roboto", "Arial", sans-serif;



img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  clip: rect(0 0 0 0);
  overflow: hidden;
}
